table {
  border-collapse: collapse;
}

td {
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  text-align: center;
  background-color: #ffffff;
}

.game-container {
  display: grid;
  position: relativ;

  margin: auto;
  color: white;
  font-size: 153%;
  font-weight: bold;
  justify-content: center;
  text-align: center;
}

.game-info {
  display: grid;
  position: relativ;
  justify-content: center;
  grid-template-columns: auto auto;
  text-align: center;
}

.game-item_1 {
  grid-column-start: 1;
  grid-column-end: 5;
  margin: 5%;
}

.game-item_2 {
  grid-column-start: 1;
  grid-column-end: 2;
}

.game-item_3 {
  grid-column-start: 2;
  grid-column-end: 2;
}

.game-item_4 {
  grid-column-start: 1;
  grid-column-end: 3;
  margin: 5%;
}

.container {
  display: grid;
  position: static;
  align-items: center;
  margin: 10% 10% 10%;
  justify-content: center;
}

.winner-message {
  color: white;
  font-size: 170%;
  font-weight: bold;
  top: 3rem;
  position: relative;
}

.game-board {
  position: static;
  align-items: center;
  margin: 5% auto;
}

.winner-info {
  color: white;
  font-size: 153%;
  font-weight: bold;
  justify-content: center;
  text-align: center;
}

.header-item {
  top: 3rem;
  color: white;
  font-size: 30px;
  position: relative;
  margin: auto;
  align-items: center;
  text-align: center;
}

.item-button {
  width: 100%;
  justify-self: center;
  background-color: #35bbca;
  border-radius: 5px;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  position: relative;
  top: 8rem;
}
