body {
    background: #f2f2f2;
  }
  
  .selectdiv {
    position: relative;
    text-align: right;
    font-family: 62, 5% 'Roboto', sans-serif;
    color: #333;
    justify-content: center;
    max-width: 120px;
    margin: 0 auto;
    padding: 0.8%;
  }
  
  select::-ms-expand {
    display: none;
  }
  
  .selectdiv:after {
    content: '<>';
    
    text-align: right;
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    right: 5px;
  
    top: 8px;
    padding: 0 0 1px;
    border-bottom: 1px solid #999;
  
    position: absolute;
    pointer-events: none;
  }
  
  .selectdiv select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    
  
    display: block;
    width: 100%;
    max-width: 320px;
    height: 50px;

    margin: 1px 0px;
    padding: 0 5px;
    font-size: 16px;
    line-height: 1.75;
    color: #333;
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #cccccc;
    -ms-word-break: normal;
    word-break: normal;
  }
  