.header-item {
  top: 3rem;
  color: white;
  font-size: 30px;
  position: relative;
  margin: auto;
  align-items: center;
  text-align: center;
}

.container {
  display: grid;
  position: static;
  align-items: center;
  margin: 15% 20% 10%;
  justify-content: center;
}

.instructions {
  color: white;
  font-size: 153%;
  font-weight: bold;
  top: 3rem;
  position: relative;
}

.item-button {
  width: 100%;
  justify-self: center;
  background-color: #35bbca;
  border-radius: 5px;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  position: relative;
  top: 8rem;
}
